* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f2f2f2;
  font-family: Arial, sans-serif;
}

header {
  background-color: #fff;
  min-width: 320px;
  padding: 15px 0;
  box-shadow: 0 2px 4px #0000001a;
}

.navbar {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  position: relative;
}

.logo img {
  width: 50px;
  height: 50px;
}

.menu {
  align-items: center;
  gap: 20px;
  list-style: none;
  display: flex;
  position: relative;
}

.menu li {
  position: relative;
}

.menu a {
  color: #000;
  padding: 5px 0;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s, border-bottom .3s;
  position: relative;
}

.menu a.active, .menu a:hover {
  color: #00adef;
}

.menu a.active:after, .menu a:hover:after {
  content: "";
  background-color: #00adef;
  border-radius: 5px;
  width: 100%;
  height: 3px;
  transition: width .3s;
  position: absolute;
  bottom: -5px;
  left: 0;
}

@media (width <= 1200px) {
  .navbar {
    max-width: 1024px;
    padding: 0 15px;
  }
}

@media (width <= 1024px) {
  .menu {
    gap: 15px;
  }

  .navbar {
    max-width: 1024px;
    padding: 0 10px;
  }
}

@media (width <= 768px) {
  .menu {
    flex-direction: column;
    gap: 10px;
  }

  .navbar {
    flex-direction: column;
    align-items: flex-start;
  }
}
